import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './Skills.module.scss';
import * as d3 from "d3";

function Skills() {
  const radius = 400;
  const autoBox = () => {
    document.body.appendChild(this);
    const { x, y, width, height } = this.getBBox();
    document.body.removeChild(this);
    return [x, y, width, height];
  };
  const partition = (data) => {
    return d3.partition()
      .size([2 * Math.PI, radius])
      (d3.hierarchy(data)
        .sum(d => d.value)
        .sort((a, b) => b.value - a.value));
  }

  const chart = (data) => {
    const root = partition(data);
    const svg = d3.select("#svgFile");
    const color = d3.scaleOrdinal(d3.quantize(d3.interpolateRainbow, data.children.length + 1));
    const format = d3.format(",d");
    const arc = d3.arc()
      .startAngle(d => d.x0)
      .endAngle(d => d.x1)
      .padAngle(d => Math.min((d.x1 - d.x0) / 2, 0.005))
      .padRadius(radius / 2)
      .innerRadius(d => d.y0)
      .outerRadius(d => d.y1 - 1);
    svg.append("g")
      .attr("fill-opacity", 0.6)
      .selectAll("path")
      .data(root.descendants().filter(d => d.depth))
      .join("path")
      .attr("fill", d => { while (d.depth > 1) d = d.parent; return color(d.data.name); })
      .attr("d", arc)
      .append("title")
      .text(d => `${d.ancestors().map(d => d.data.name).reverse().join("/")}\n${format(d.value)}`);

    svg.append("g")
      .attr("pointer-events", "none")
      .attr("text-anchor", "middle")
      .attr("font-size", 10)
      .attr("font-family", "sans-serif")
      .selectAll("text")
      .data(root.descendants().filter(d => d.depth && (d.y0 + d.y1) / 2 * (d.x1 - d.x0) > 10))
      .join("text")
      .attr("transform", function (d) {
        const x = (d.x0 + d.x1) / 2 * 180 / Math.PI;
        const y = (d.y0 + d.y1) / 2;
        return `rotate(${x - 90}) translate(${y},0) rotate(${x < 180 ? 0 : 180})`;
      })
      .attr("dy", "0.35em")
      .text(d => d.data.name);

    const node = svg.attr("viewBox", [-400, -400, 800, 800]).node();
    return node;
  };
  useEffect(() => {
    const skilSet = chart({
      "name": "Skills",
      "children": [
        {
          "name": "Programming",
          "children": [
            {
              "name": "JavaScript",
              "children": [
                { "name": "Angular", "value": 1000 },
                { "name": "React", "value": 1000 },
                { "name": "Rxjs", "value": 1000 },
                { "name": "Redux", "value": 1000 },
                {
                  "name": "NodeJs", "children": [
                    { "name": "ExpressJs", "value": 1000, },
                    { "name": "NestJs", "value": 1000 },
                    { "name": "Mongoose", "value": 1000 },
                    { "name": "Sequelize", "value": 1000 },
                    { "name": "Passport", "value": 1000 },
                    { "name": "Mocha", "value": 1000 }
                  ]
                }
              ]
            },
            {
              "name": "C#",
              "children": [
                { "name": ".NET", "value": 1000 },
                { "name": "Dotnet Core", "value": 1000 },
                { "name": "EF", "value": 1000 },
                { "name": "LINQ", "value": 1000 },
                { "name": "WCF", "value": 1000 },
                { "name": "Web API", "value": 1000 },
                { "name": "MVC", "value": 1000 },
                { "name": "MSTest", "value": 1000 },
              ]
            },
            {
              "name": "Python",
              "children": [
                { "name": "Flask", "value": 1000 },
                { "name": "SQLAlchemy", "value": 1000 }
              ]
            }
          ]
        },
        {
          "name": "DevOps",
          "children": [
            { "name": "Jenkins", "value": 1000 },
            { "name": "Docker", "value": 1000 },
            { "name": "Kubernetes", "value": 1000 }
          ]
        },
        {
          "name": "Database",
          "children": [
            { "name": "SQL Server", "value": 1000 },
            { "name": "PostgreSQL", "value": 1000 },
            { "name": "MongoDb", "value": 1000 }
          ]
        },
        {
          "name": "VCS",
          "children": [
            {
              "name": "Git", "children": [
                { "name": "Github", "value": 1000 },
                { "name": "Bitbucket", "value": 1000 },
                { "name": "Gitlab", "value": 1000 },
              ]
            },
            { "name": "NX", "value": 1000 },
          ]
        },
        {
          "name": "Tools",
          "children": [
            { "name": "Jira", "value": 1000 },
            { "name": "Confluence", "value": 1000 }
          ]
        },
        {
          "name": "OS",
          "children": [
            { "name": "Windows", "value": 1000 },
            { "name": "Linux", "value": 1000 }
          ]
        },
        {
          "name": "Cloud",
          "children": [
            { "name": "AWS", "value": 1000 },
            { "name": "Azure", "value": 1000 },
          ]
        },
        {
          "name": "Architecture",
          "children": [
            { "name": "Monolith", "value": 1616 },
            { "name": "Microservices", "value": 1616 }
          ]
        }
      ]
    }
    );
  });
  return (
    <div className={styles.Skills}>
      <svg id="svgFile"></svg>
    </div>
  );
}

Skills.propTypes = {};

Skills.defaultProps = {};

export default Skills;
